/**
* Import styles as SMACSS methodology
*/
@import url("https://fonts.googleapis.com/css?family=Roboto:400,500,500i,700&subset=cyrillic");
html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  font-weight: 100;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

ul {
  list-style: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

/* box model for all elements */
*, *:after, *:before {
  box-sizing: border-box;
}

/**
* Main container
*/
[class$="__container"] {
  width: 100%;
  max-width: 1230px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
  position: relative;
}

[class$="__container"]:before {
  content: '';
  display: table;
}

[class$="__container"]:after {
  content: '';
  display: table;
  clear: both;
}

/**
* Fluid container
*/
[class^="container--fluid"] {
  max-width: 1280px;
}

/**
* Row class
*/
[class$="--row"] {
  margin: 0 -20px;
}

/**
* Block floating
*/
.block {
  width: 100%;
  float: left;
  overflow: hidden;
}

/**
 * Overlay styles
 */
.overlay {
  position: relative;
}

.overlay:before {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}

.overlay--dark:before {
  background: rgba(40, 37, 34, 0.9);
}

.overlay--light:before {
  background: rgba(255, 255, 255, 0.8);
}

.heading {
  color: #282522;
  line-height: 1.2;
}

.heading--h1 {
  font-size: 3.125em;
  font-weight: bold;
  text-transform: uppercase;
  margin-top: 30px;
  margin-bottom: 30px;
}

.heading--med {
  font-size: 2.5em;
  font-weight: bold;
  text-transform: uppercase;
  margin-top: 10px;
  margin-bottom: 40px;
}

.heading--h2 {
  font-size: 1.625em;
  font-weight: bold;
  line-height: 1.2;
  margin-top: 10px;
  margin-bottom: 50px;
}

.heading--h3 {
  font-size: 1.25em;
  letter-spacing: 4px;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 50px;
}

.heading--h4 {
  font-size: 1.25em;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 45px;
}

.heading--center {
  text-align: center;
}

.heading--white {
  color: #fff;
}

.heading--gray {
  color: #4c4c4c;
}

.text {
  color: #282522;
  font-size: 1em;
  line-height: 1.4;
  margin-bottom: 10px;
}

.text--white {
  color: #fff;
}

.breadcrumb {
  width: 100%;
  float: left;
  text-align: center;
  margin-bottom: 20px;
}

.breadcrumb__item {
  display: inline-block;
  margin-right: 5px;
}

.breadcrumb__item:after {
  content: ' / ';
  color: #f5f5f5;
  text-decoration: none;
  font-size: 0.8125em;
  opacity: .8;
}

.breadcrumb__item:last-child:after {
  display: none;
}

.breadcrumb__item:last-child .breadcrumb__link {
  color: #f5f5f5;
  opacity: 1;
}

.breadcrumb__link {
  color: #f5f5f5;
  text-decoration: none;
  font-size: 0.8125em;
  padding: 10px;
  opacity: .8;
}

.asistent {
  display: flex;
}

.asistent__info {
  padding-left: 30px;
}

.asistent .employer__desc {
  color: #4c4c4c;
}

.bottom-tab {
  width: 100%;
}

.bottom-tab__header {
  width: 100%;
  float: left;
  border-bottom: 1px solid #fff;
}

.bottom-tab__item {
  display: inline-block;
  width: 33.3%;
  float: left;
}

.bottom-tab__link {
  width: 100%;
  padding: 12px;
  float: left;
  transition: all .2s linear;
  text-align: center;
  font-size: 0.8125em;
  color: #9f9f9f;
  text-decoration: none;
}

.bottom-tab__block {
  width: 100%;
  float: left;
  display: none;
}

.bottom-tab__line {
  width: 100%;
  float: left;
  padding-top: 24px;
  display: flex;
  align-items: center;
}

.bottom-tab__text {
  color: #9f9f9f;
  font-size: 0.8125em;
  line-height: 1.5;
  width: calc(100% - 50px);
  padding-left: 24px;
}

.social {
  display: flex;
  align-items: center;
  justify-content: center;
  float: left;
  margin: 3px;
  width: 35px;
  height: 35px;
  background: #282522;
  border: none;
}

.social:hover {
  background: #e21f26;
}

.button {
  padding: 15px 45px;
  background: #e21f26;
  float: left;
  border: none;
  color: #fff;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 0.8125em;
  transition: all .3s ease;
}

.button:hover, .button:focus {
  background: #282522;
}

.button--ico {
  padding: 10px 30px;
  display: flex;
  align-items: center;
}

.button--ico > .icon {
  margin-right: 15px;
}

.callme {
  width: 50px;
  height: 50px;
  background: #e21f26;
  position: fixed;
  left: 50px;
  bottom: 50px;
  z-index: 99;
  border-radius: 50%;
  text-align: center;
  line-height: 55px;
  outline: none;
  animation: callme  2s infinite;
}

.callme:hover {
  animation: none;
}

.callme > .icon {
  display: inline-block;
}

.call-dialog {
  background: white;
  padding: 20px 30px;
  text-align: left;
  max-width: 400px;
  margin: 40px auto;
  position: relative;
}

.call-dialog:after {
  content: '';
  display: table;
  clear: both;
}

.cascade-slider_container {
  position: relative;
  height: 500px;
  margin: 0 auto;
  user-select: none;
}

.cascade-slider_item {
  position: absolute;
  text-align: center;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%) scale(0.3);
  transition: all 1s ease;
  opacity: 0;
  z-index: -1;
}

.cascade-slider_thumbnail {
  box-shadow: 0 8px 18px rgba(0, 0, 0, 0.35);
  width: 560px;
  height: 315px;
}

.cascade-slider_caption {
  font-size: 12px;
  line-height: 1.5;
  color: #282522;
  text-align: center;
  margin-top: 30px;
  opacity: 0;
  transition: opacity 1s ease;
}

.cascade-slider_item.next {
  left: 50%;
  transform: translateY(-50%) translateX(-120%) scale(0.8);
  opacity: 1;
  z-index: 1;
}

.cascade-slider_item.prev {
  left: 50%;
  transform: translateY(-50%) translateX(20%) scale(0.8);
  opacity: 1;
  z-index: 1;
}

.cascade-slider_item.now {
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%) scale(1.2);
  opacity: 1;
  z-index: 5;
}

.cascade-slider_item.now .cascade-slider_caption {
  opacity: 1;
}

.cascade-slider_arrow {
  position: absolute;
  width: 55px;
  height: 55px;
  border: none;
  background: transparent;
  padding: 0;
  font-size: 0;
  cursor: pointer;
  outline: none;
  z-index: 5;
}

.cascade-slider_arrow:before {
  content: '';
  display: block;
  width: 13px;
  height: 21px;
  background: url(../img/sprite/arrow-dark.png#sprite);
  position: absolute;
  left: 50%;
  top: 50%;
  margin-top: -10.5px;
  margin-left: -6.5px;
}

.cascade-slider_arrow-left {
  right: 55px;
  top: 50%;
  transform: translateY(-50%) rotate(180deg);
}

.cascade-slider_arrow-right {
  left: 55px;
  top: 50%;
  transform: translateY(-50%);
}

.cascade-slider_nav {
  position: absolute;
  bottom: -120px;
  width: 100%;
  text-align: center;
  z-index: 5;
}

.cascade-slider_dot {
  display: inline-block;
  width: 1em;
  height: 1em;
  margin: 1em;
  background: #ddd;
  list-style: none;
  cursor: pointer;
}

.cascade-slider_dot:hover {
  background: #555;
}

.cascade-slider_dot.cur {
  background: #555;
}

.video-slide {
  position: relative;
}

.video-slide__container {
  position: static;
}

.testimonials {
  background: url(../img/offer-bg.jpg);
  background-position: top center;
  background-size: cover;
}

.comment {
  width: 100%;
  float: left;
  display: flex;
  justify-content: space-between;
}

.comment__item {
  max-width: 370px;
  float: left;
}

.comment__text {
  display: block;
  padding: 24px;
  background: #fff;
  border-radius: 5px;
  box-shadow: 0 3px 7px rgba(0, 0, 0, 0.35);
  color: #282522;
  font-style: italic;
  font-size: 0.875em;
  line-height: 1.5;
  position: relative;
}

.comment__text::after {
  content: '';
  display: block;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 8px solid #fff;
  position: absolute;
  left: 35px;
  bottom: -8px;
}

.comment__owner {
  display: flex;
  align-items: center;
  padding-top: 30px;
}

.comment__avatar {
  width: 100px;
  height: 100px;
  box-shadow: 0 3px 7px rgba(0, 0, 0, 0.35);
  border: 4px solid #fff;
  border-radius: 50%;
  margin-right: 15px;
}

.comment__owner-name {
  color: #fff;
  font-size: 1em;
  line-height: 1.3;
  font-weight: bold;
  margin-bottom: 10px;
}

.comment__date {
  font-size: 0.75em;
  color: #9f9f9f;
  font-style: italic;
}

.contact-form {
  display: block;
  float: left;
  width: 100%;
  margin-top: 20px;
}

.contact-form__input {
  width: 100%;
  height: 45px;
  border: 1px solid #e0e0e0;
  margin-bottom: 10px;
  padding-left: 45px;
  padding-right: 15px;
}

.contact-form__input:focus {
  outline-color: #e21f26;
}

.contact-form__btn {
  width: 100%;
  height: 45px;
}

.contact-form__input-group {
  width: 100%;
  float: left;
  position: relative;
}

.contact-form__input-group > .icon {
  position: absolute;
  left: 17px;
  top: 17px;
}

.employers-timeline {
  width: 100%;
  float: left;
  margin-bottom: 75px;
  position: relative;
}

.employers-timeline::before {
  content: '';
  display: block;
  width: 1px;
  height: 100%;
  position: absolute;
  left: 50%;
  margin-left: -1px;
  top: 0;
  background: #e0e0e0;
  z-index: -1;
}

.employer {
  display: flex;
  width: 100%;
  padding: 18px 0;
  position: relative;
  justify-content: flex-end;
}

.employer:after {
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  background: #e0e0e0;
  position: absolute;
  top: 50%;
  left: 0;
  margin-top: -1px;
  z-index: -1;
}

.employer:first-child {
  padding-top: 0;
}

.employer:last-child {
  padding-bottom: 0;
}

.employer:nth-child(odd) {
  flex-direction: row-reverse;
  text-align: right;
}

.employer__thumbnail {
  border: 1.5px solid #e0e0e0;
  border-radius: 50%;
  display: flex;
  align-self: center;
  float: left;
}

.employer__avatar {
  width: 174px;
  height: 174px;
  border: 10px solid #fff;
  border-radius: 50%;
  float: left;
}

.employer__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: calc(50% - 88px);
  padding: 0 30px;
  background: #fff;
}

.employer__name {
  font-size: 1.375em;
  color: #282522;
  margin-bottom: 11px;
}

.employer__position {
  color: #e21f26;
  font-size: 0.75em;
  text-transform: uppercase;
  margin-bottom: 25px;
}

.employer__desc {
  font-size: 0.875em;
  color: #9f9f9f;
  line-height: 1.5;
}

.footer-nav {
  display: flex;
  justify-content: space-between;
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
}

.footer-nav__link {
  font-size: 0.875em;
  color: #9f9f9f;
  text-decoration: none;
  padding: 5px;
}

.footer-nav__link:hover {
  color: #fff;
}

/**
 * Footer styles
 */
.footer {
  background: url(../img/footer-bg.jpg) no-repeat transparent;
  background-position: top center;
  background-size: cover;
  padding-top: 50px;
}

.footer__container {
  display: flex;
  justify-content: space-between;
}

.footer__heading {
  color: #fff;
  text-transform: uppercase;
  font-size: 1em;
  margin-bottom: 30px;
  padding-bottom: 17px;
  display: block;
  position: relative;
}

.footer__heading:after {
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  background: #e21f26;
  position: absolute;
  left: 0;
  bottom: 0;
}

.footer__column {
  display: flex;
  flex: 1;
  flex-direction: column;
}

.footer__column:nth-child(1) {
  padding-right: 50px;
}

.footer__column:nth-child(2) {
  text-align: center;
  align-items: center;
  justify-content: space-between;
  padding: 0 25px;
}

.footer__column:nth-child(3) {
  padding-left: 50px;
}

.footer__column:nth-child(3) .footer__heading {
  text-align: right;
}

.copyright {
  width: 100%;
  margin-top: 50px;
  padding: 25px 0;
  display: flex;
  justify-content: space-between;
  border-top: 1px solid #9f9f9f;
}

.copyright__text {
  font-size: 0.75em;
  color: #9f9f9f;
}

.copyright__link {
  color: #fff;
  text-decoration: none;
}

.gallery {
  width: 66.6%;
  float: left;
}

.gallery__show {
  display: block;
  float: left;
  width: 25%;
  position: relative;
  outline: none;
}

.gallery__show::before {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  background: rgba(40, 37, 34, 0.8);
  position: absolute;
  left: 0;
  top: 0;
  transition: opacity .3s ease;
  opacity: 0;
}

.gallery__show::after {
  content: '';
  display: block;
  width: 32px;
  height: 32px;
  background: url(../img/sprite/m-glass.png#sprite);
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -16px;
  margin-top: -16px;
  transition: transform .3s ease;
  transform: scale(0);
}

.gallery__show:hover::before {
  opacity: 1;
}

.gallery__show:hover::after {
  transform: scale(1);
}

.gallery__img {
  display: block;
  float: left;
  width: 100%;
  height: 100%;
}

.go-up {
  width: 50px;
  height: 50px;
  background: #e21f26;
  position: fixed;
  right: 50px;
  bottom: 50px;
  z-index: 99;
  border-radius: 50%;
  text-align: center;
  line-height: 55px;
  display: none;
}

.go-up > .icon {
  transform: rotate(90deg);
  display: inline-block;
}

.hamburger {
  width: 50px;
  height: 50px;
  border: none;
  padding: 10px;
  position: relative;
  background: #fff;
  outline: none;
  font-size: 0;
  text-align: center;
}

.hamburger__item {
  display: inline-block;
  width: 30px;
  height: 2px;
  margin: 3px 0;
  background: #e21f26;
  transition: all .3s ease;
}

/**
 * Header styles
 */
.header {
  height: 200px;
  background: url(../img/heder-bg.jpg) no-repeat transparent;
  background-position: top center;
  background-size: cover;
}

.header__container {
  display: flex;
  height: 100%;
}

.header__social {
  padding: 7px 0;
}

.header__info {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  border-left: 1px solid #e0e0e0;
  border-right: 1px solid #e0e0e0;
}

.header__top {
  width: 100%;
  display: flex;
  justify-content: space-around;
  height: 55px;
  border-bottom: 1px solid #e0e0e0;
}

.header__contacts {
  flex-grow: 1;
}

.header__tab-column {
  width: 33.3333%;
  float: left;
  height: 100%;
  border-right: 1px solid #e0e0e0;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
}

.header__tab-column > .icon {
  margin: 15px;
}

.header__tab-column:last-child {
  border-right: none;
}

.header__tab-text, .header__tab-link {
  color: #282522;
  text-decoration: none;
  font-size: 0.875em;
  line-height: 1.5;
}

