.jumbotron {
  display: flex;
}

.jumbotron__container {
  width: 100%;
  max-width: 630px;
  margin: 0;
  padding-left: 50px;
  padding-right: 50px;
  padding-bottom: 20px;
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: center;
}

.jumbotron__left {
  background: url(../img/contact-form-left-bg.jpg) no-repeat;
  padding: 100px 0;
  background-position: top center;
  background-size: cover;
  width: 50%;
  float: left;
  position: relative;
}

.jumbotron__left [class$="__container"] {
  float: right;
}

.jumbotron__left .jumbotron__container::after {
  display: block;
  width: 80%;
  height: 2px;
  background: #e21f26;
  position: absolute;
  left: 10%;
  bottom: 0;
}

.jumbotron__right {
  background: url(../img/contact-form-right-bg.jpg) no-repeat;
  padding: 100px 0;
  background-position: top center;
  background-size: cover;
  width: 50%;
  float: left;
  position: relative;
}

.jumbotron__right [class$="__container"] {
  float: left;
}

.jumbotron__right .jumbotron__container:before {
  content: '';
  display: block;
  width: 0;
  height: 0;
