  left: 50%;
  top: 50%;
  margin-top: -10.5px;
  margin-left: -6.5px;
}

.slick-prev {
  left: 55px;
  top: 50%;
  transform: translateY(-50%);
}

.slick-next {
  right: 55px;
  top: 50%;
  transform: translateY(-50%) rotate(180deg);
}

