/**
 * Animations 
 */
@keyframes callme {
  0%, 20% {
    transform: scale(1);
    box-shadow: 0px 0px 0px 0px rgba(226, 31, 38, 0.5);
  }
  50% {
    transform: scale(0.8);
    box-shadow: 0px 0px 0px 50px rgba(226, 31, 38, 0);
  }
  80%, 100% {
    transform: scale(1);
  }
}

/**
* JavaScript states
*/
.main-nav__link.active {
  border-bottom: 3px solid #e21f26;
  color: #e21f26;
}

.tab__link.active {
  background: #e21f26;
  color: #fff;
}

.tab__link.active:after {
  content: '';
  display: block;
  width: 16px;
  height: 16px;
  background: #e21f26;
  position: absolute;
  left: 50%;
  bottom: -8px;
  margin-left: -8px;
  transform: rotate(-45deg);
}

.tab__content.current, .bottom-tab__block.current {
  display: block;
}

.sticky-wrapper {
  width: 100%;
  float: left;
  background: #fff;
}

.sticky-wrapper.is-sticky .main-nav {
  z-index: 9 !important;
}

.is-sticky .main-nav__logo {
  width: auto;
  padding: 14px;
}

.is-sticky .main-nav__logo > img {
  width: 158px;
}

.offer__tile.hovered .offer__caption {
  color: #e21f26;
}

.offer__tile.hovered .offer__overlay::after {
  opacity: 1;
}

.bottom-tab__link.active {
  background: #fff;
  color: #282522;
}

.hamburger.active .hamburger__item:nth-child(1) {
  width: 15px;
  transform: rotate(45deg);
  transform-origin: 9px 13px;
}

.hamburger.active .hamburger__item:nth-child(3) {
  width: 15px;
  transform: rotate(-45deg);
  transform-origin: 9px -11px;
}

.mobile-menu__link.active .mobile-menu__carret {
  transform: rotate(180deg);
}

.mobile-menu__inner.shown {
  right: 0;
}

.mobile-menu__submenu.shown {
  height: auto;
}

.js-map.focused > .map__iframe {
  pointer-events: all;
}

/**
 * This site have only one theme
 */
/**
 * Responsive
 */
/**
* Breakpoint sizes
*/
/**
 * 1440px
 */
@media only screen and (max-width: 1441px) {
  [class$="__container"] {
    max-width: 1200px;
  }
  .slick-prev {
    left: 0;
  }
  .slick-next {
    right: 0;
  }
  .cascade-slider_thumbnail {
    width: 460px;
    height: 290px;
  }
}

/**
 * 1200px
 */
@media only screen and (max-width: 1201px) {
  [class$="__container"] {
    max-width: 1140px;
  }
  .cascade-slider_thumbnail {
    width: 420px;
    height: 250px;
  }
}

/**
 * 1250px
 */
@media only screen and (max-width: 1251px) {
  .header {
    height: 160px;
  }
  .logo {
    padding: 5px;
  }
  .logo__ico {
    max-width: 80%;
  }
  .top-nav__link {
    padding: 15px;
    font-size: 0.75em;
  }
  .tab__link {
    padding: 0 10px;
    font-size: 0.75em;
  }
  .main-nav {
    height: 60px;
  }
  .main-nav__link {
    padding: 8px;
    line-height: 1.5;
    font-size: 0.75em;
  }
  .callme {
    left: 30px;
    bottom: 30px;
  }
  .go-up {
    right: 30px;
    bottom: 30px;
  }
  .heading--h1 {
    font-size: 2.1875em;
  }
