.map{
    border-top: 8px solid $color--black2;
    position: relative;
    padding-bottom: 400px;
    height: 0;
    overflow: hidden;
    
    &__iframe{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        pointer-events: none;
    }
}